import shoppingcatlist from "@/components/shoppingcartlist/shoppingcartlist.vue"
import shopingbottompay from "@/components/shoppingcartlist/shopingbottompay.vue"
const app = getApp();
export default {
	
	data() {
		return {
			totalpic:0,
			bool:true,
			zizujianlist:[],
			xiabiao:null,
			returnsindex:[]
		}
	},
	methods: {
		getChild1(e){
			// console.log(e)
			if(e){
				this.totalpic = e
			}else{
				this.totalpic = 0
			}
		},
		//这是传过来的数组
		deteledatalist(e){
			this.zizujianlist = e
		},
		//这是传过来的下标
		deteledatasubscript(e){
			this.xiabiao = e
		},
		//这是shopingbottompay组件传过来的删除后的数组  在将下标返回去删除
		deteindexdata(e){
			this.returnsindex = e
			// console.log(e) 这是删除后的数组
		},
			// console.log(this.totalpic)
		//这是管理
		showmanagement(){
			if(this.bool){
				this.bool = false
			}else{
				this.bool = true
			}
		}
	},
	onLoad(){
		this.statusBar = app.globalData.statusBar
	},
	onShow(){
		
	},
	created(){
		//父组件事件接收子组件传过来的值
		this.getChild1()
		// this.deteledatalist()
		// this.deteledatasubscript()
	},
	components:{
		shoppingcatlist,
		shopingbottompay
	}
}
